import './index.scss';

import classNames from 'classnames';
import { HTMLAttributes } from 'react';

// ----------------------------------------------------------------------

export type DividerProps = {
  type?: 'solid' | 'dotted' | 'dashed';
  isHorizontal?: boolean;
  spacing?: 4 | 8 | 12 | 16 | 20 | 24 | 28 | 32;
  color?: string;
} & HTMLAttributes<HTMLDivElement>;

const MARGIN_X_MAPS = {
  4: 'mx-1',
  8: 'mx-2',
  12: 'mx-3',
  16: 'mx-4',
  20: 'mx-5',
  24: 'mx-6',
  28: 'mx-7',
  32: 'mx-8'
};

const MARGIN_Y_MAPS = {
  4: 'my-1',
  8: 'my-2',
  12: 'my-3',
  16: 'my-4',
  20: 'my-5',
  24: 'my-6',
  28: 'my-7',
  32: 'my-8'
};

const TYPE_MAPS = {
  solid: 'qb-divider--solid',
  dotted: 'qb-divider--dotted',
  dashed: 'qb-divider--dashed'
};

const Divider = ({
  type = 'solid',
  isHorizontal = true,
  spacing = 20,
  color = 'border-gray-400',
  className
}: DividerProps) => {
  return isHorizontal ? (
    <div
      className={classNames(
        'qb-divider flex w-full h-px border-b',
        TYPE_MAPS[type],
        MARGIN_Y_MAPS[spacing],
        color ? color : 'qb-divider--white',
        className
      )}
    />
  ) : (
    <div
      className={classNames(
        'qb-divider inline-flex shrink-0 w-px h-full border-l',
        TYPE_MAPS[type],
        MARGIN_X_MAPS[spacing],
        color ? color : 'qb-divider--white',
        className
      )}
    />
  );
};

export default Divider;
