import './index.scss';

import classNames from 'classnames';
import { PropsWithChildren } from 'react';

// ----------------------------------------------------------------------

export interface ButtonGroupProps extends PropsWithChildren {
  flexDirection?: 'row' | 'column';
  isFullWidth?: boolean;
  className?: string;
}

const ButtonGroup = ({
  children,
  flexDirection = 'row',
  isFullWidth = false,
  className
}: ButtonGroupProps) => {
  return (
    <div
      className={classNames(
        'button-group',
        flexDirection === 'row' ? 'button-group--row' : 'button-group--column',
        { 'button-group--full': isFullWidth },
        className
      )}
    >
      {children}
    </div>
  );
};

export default ButtonGroup;
