import { ReactNode } from 'react';

import Typography from 'components/ui/Typography';

import classNames from 'classnames';

// ----------------------------------------------------------------------

export type LabelProps = {
  width?: string;
  text: ReactNode;
  suffix?: ReactNode;
  required?: boolean;
};

const Label = ({ width, text, required = false, suffix }: LabelProps) => {
  return (
    <div className="qb-label inline-flex items-center">
      <Typography weight={700} className={classNames('items-center', width)}>
        {text}
      </Typography>
      {suffix && <div className="inline-flex ml-1">{suffix}</div>}
      {required && <i className="inline-flex ml-2 text-red-light">*</i>}
    </div>
  );
};

export default Label;
